import React from 'react';
import type { FC } from 'react';
import type { UserInfo } from '@wix/cli-auth';
import { Text, ConfirmInput } from '@wix/cli-ui-kit';
import { Trans, useTranslation } from '@wix/cli-i18n';

interface Props {
  userInfo: UserInfo;
  onSubmit: (relogin: boolean) => void;
}

export const AlreadyLoggedIn: FC<Props> = ({ userInfo, onSubmit }) => {
  const { t } = useTranslation();

  return (
    <>
      <Trans
        i18nKey="login_command.already_logged_in.warning"
        components={[<Text skin="info" />]}
        values={{ email: userInfo.email }}
      />
      <ConfirmInput
        label={t('login_command.already_logged_in.confirm')}
        initialValue={true}
        onSubmit={onSubmit}
      />
    </>
  );
};
